.ant-btn {
    position: relative;
    display: inline-block;
    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    background-image: none;
    border: 1px solid transparent;
    box-shadow: 0 2px 0 rgba(0, 0, 0, 0.015);
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    user-select: none;
    touch-action: manipulation;
  }
  
  .ant-btn-primary {
    color: #fff;
    background-color: #1890ff;
    border-color: #1890ff;
  }
  
  .ant-input {
    display: inline-block;
    width: 100%;
    height: 32px;
    padding: 4px 11px;
    font-size: 14px;
    line-height: 1.5;
    color: rgba(0, 0, 0, 0.65);
    background-color: #fff;
    border: 1px solid #d9d9d9;
    border-radius: 2px;
    transition: all 0.3s;
  }
  
  .ant-table {
    width: 100%;
    background: #fff;
    color: rgba(0, 0, 0, 0.85);
    border-radius: 2px;
    margin-top: 16px;
    overflow: hidden;
  }
  
  .ant-card {
    position: relative;
    background: #fff;
    border-radius: 2px;
    transition: box-shadow 0.3s;
    border: 1px solid #f0f0f0;
  }
  